*,
*::after,
*::before{
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    
    font-size: 18px;
    font-family: 'Open Sans', sans-serif;
    background-color: hsl(233, 47%, 7%);
}
section .Card {
    width: 90%;
    margin: 50px auto;
    background-color: hsl(244, 38%, 16%);
    border-radius: 10px;
    text-align: center;
}
section .Card img {
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    filter: grayscale(100%) brightness(40%) sepia(100%) hue-rotate(230deg) saturate(600%) contrast(0.8);
}
section .Card .Text {
    padding: 0 20px 10px ;
}
section .Card .Text h1 {
    color: hsl(0, 0%, 100%);
    font-weight: 400px;
    font-size: 2.5rem;
}
section .Card .Text h1 span {
    color: hsl(277, 64%, 61%);
}
section .Card .Text p {
    color: hsla(0, 0%, 100%, 0.6);
    font-size: 1.2rem;
    font-weight: 400;
}
section .Card .Text h2 {
    color: hsla(0, 0%, 100%, 0.6);
    font-size: 2.5rem;
    margin-bottom: 0;
}
section .Card .Text h3 {
    color: hsla(0, 0%, 100%, 0.6);
    text-transform: uppercase;
    font-size: 1.2rem;
    font-weight: 400;
    margin-top: 5px;
}
@media screen and (min-width: 900px) {
    section .Card {
        display: flex;
        flex-direction: row-reverse;
        /* gap: 60px; */
        text-align: start;
    }
    section .Card img {
        max-width: 50%;
        border-top-left-radius: 0;
        border-bottom-right-radius: 10px;
    }
    section .Card .Text {
        padding: 60px;
    }
    section .Card .Text .D {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 30px;
    }
} 